<template>
    <div id="edit_blog" style="background:#eee;padding: 20px">
      <h2>添加博客</h2>
      <form v-if="!submitd">
        <label>博客标题</label>
        <Input v-model="blog.title" required placeholder="Enter title..." style="width: 100%" />
        <label>博客内容</label>
        <Input v-model="blog.content" type="textarea" :rows="5" placeholder="Enter content..." />
  <div id="checkbox">
    <CheckboxGroup v-model="blog.blogTypes" :value="blog.blogTypes" :label="blog.blogTypes" size="large">
        <checkbox label="生活">
            <Icon type="ios-shirt" />
            <span>生活</span>
        </Checkbox>
        <Checkbox label="娱乐">
            <Icon type="md-musical-note" />
            <span>娱乐</span>
        </Checkbox>
        <Checkbox label="运动">
            <Icon type="ios-basketball" />
            <span>运动</span>
        </Checkbox>
        <Checkbox label="心得">
            <Icon type="md-heart" />
            <span>心得</span>
        </Checkbox>
    </CheckboxGroup>
  </div>
      <label>作者</label>
      <Select v-model="blog.author" style="width:100%">
        <Option v-for="author in authors" :value="author" :key="author">
            <span>{{author}}</span>
        </Option>
    </Select>
      <Button type="success" v-on:click.prevent="put" size="large" >保存并发布</Button>
      </form>
      <div v-if="submitd">
        <Alert type="success" show-icon>
        <h3>博客发布成功!</h3>
        <span slot="desc"></span>
        </Alert>
      </div>
      
      <div class="preview">
        <Card :bordered="false">
            <h2>博客预览</h2>
            <h4>博客标题:</h4>
            <p>{{blog.title}}</p>
            <hr>
            <h4>博客内容:</h4>
            <p>{{blog.content}}</p>
            <hr>
            <h4>博客分类：</h4>
            <ul>
              <li v-for="type in blog.blogTypes">
                {{type}}
              </li>
            </ul>
            <hr>
            <h4>作者：</h4>
            <p>{{blog.author}}</p>
        </Card>
    </div>
    </div>
</template>
 
<script>  
export default {
  name: 'edit',
  data () {
    return {
      id:this.$route.params.id,
      blog:{
        
      },
      submitd:false,
      authors:['apeng','gouyang','shaobin']
    }
  },
  methods:{
    put:function(){
      var date=new Date();
      this.blog.time=date.getFullYear()+"."+(date.getMonth()+1)+"."+date.getDate()+" "+date.getHours()+":"+date.getMinutes();
      this.$http.put('https://wd0181130895wpybmf.wilddogio.com/posts/'+this.id+".json",this.blog).then(function(data){
        // console.log(data);
        this.submitd=true;
      })
    }
  },
  created(){
  //  console.log(this.id);
    this.$http.get('https://wd0181130895wpybmf.wilddogio.com/posts/'+this.id+".json").then(function(response){
      return response.json();
    }).then(function(data){
      this.blog=data;
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#edit_blog *{
  box-sizing: border-box;
  list-style: none;
}
h4,p{
  display: inline;
  line-height: 40px;
}
.preview ul, .preview li{
  display: inline;
  line-height: 40px;
}
#edit_blog{
  box-sizing: border-box;
  margin: 20px auto;
  max-width: 700px;
  padding: 20px;
  padding-top: 0px;
}
label{
  display: block;
  padding: 10px 0 10px;
}
input[type="text"],select,textarea{
  display: block;
  padding: 10px;
  width: 100%;
}
textarea{
  height: 200px;
}
#checkbox input,#checkbox label{
  display: inline-block;
}
#checkbox input{
  margin-right: 20px;
}
Button{
  margin:20px 0;
}

</style>



