<template>
    <div id="showblog">
      <h1>博客总览</h1>
      <Input search placeholder="Enter something..." v-model="search"/>
      <!-- <input type="text" v-model="search"> -->
      <br>
      <div style="background:#eee;padding: 20px" v-for="blog in filterBlogs">
        <Card :bordered="false"><router-link v-bind:to="'blog/'+blog.id">
            <h3 v-rainbow>{{blog.title | to-uppercase}}</h3>
            <p>{{blog.content | snippet}}</p>
            <span>{{blog.time}}</span>
        </router-link>
        </Card>
    </div>
      <!-- <div v-for="blog in filterBlogs" class="single-blog">
          <router-link v-bind:to="'blog/'+blog.id"><h2 v-rainbow>{{blog.title | to-uppercase}}</h2></router-link>
          <p>{{blog.content | snippet}}</p>
      </div> -->
    </div>
</template>
 
<script>  
export default {
  name: 'Showblog',
  data () {
    return {
      blogs:[],
      search:'', 
    }
  },
  created(){
    this.$http.get('https://wd0181130895wpybmf.wilddogio.com/posts.json').then(function(data){
      // this.blogs=data.body.slice(0,10);
      //  console.log(data);
      //  console.log(data.json());
      return data.json();
    }).then(function(data){
      var blogsArray=[];
      for(let key in data){
        
       // console.log(key);
       // console.log(data[key]);
       data[key].id=key;
       blogsArray.unshift(data[key]);
       console.log(blogsArray);
      }
      this.blogs=blogsArray;
    })
  },
  computed:{
    filterBlogs:function(){
      return this.blogs.filter((blog)=>{
        return blog.title.match(this.search);
      })
    }
  },
  //自定义指令
  directives:{
   'rainbow':{
    bind(el,binding,vnode){
    el.style.color="#"+Math.random().toString(16).slice(2,8);
  }},
  },
  //过滤器
  filters:{
    'to-uppercase':function(value){
    return value.toUpperCase();
    },
    'snippet':function(value){
    return value.slice(0,100)+"...";
  },
  },
  methods:{
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#showblog{
  max-width: 700px;
  margin: 0 auto;
  position: relative;
}
#showblog h1{
  line-height: 50px;
  margin-left: 20px;
}
#showblog h3{
  line-height: 30px;
}
#showblog span{
  color: #ccc;
  position: absolute;
  right:5px;
  bottom:0px;
}

</style>



