//   return value.toUpperCase();
// })
// Vue.filter('snippet',function(value){
//   return value.slice(0,100)+"...";
// })
const router =new VueRouter({
    routes:Routes,
    // mode:"history",
    // redirect:"/",
    linkActiveClass:'',
    linkExactActiveClass:''
})
new Vue({
  el: '#app',
  components: { App },
  template: '<App/>',
  router:router,
  
})



