<template>
    <Menu mode="horizontal" :theme="theme1" active-name="1" class="header">
        <MenuItem name="1"><router-link to="/">
        <h3><Icon type="ios-paper" />
           博客列表</h3>
        </router-link></MenuItem>
        <MenuItem name="2"><router-link to="/add">
        <h3><Icon type="ios-create" />
           添加博客</h3>
        </router-link></MenuItem>
    </Menu></Col>
    
</template>
 
<script>  
export default {
  name: 'BlogHeader',
  data () {
    return {
      theme1: 'light'
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a{
  color: #000;
  
}
.header{
  max-width: 700px;
  margin: 0 auto;
}
</style>



