<template>

   <div id="single_blog">
     <Card :bordered="false">
            <h3>{{blog.title}}</h3>
            <hr>
            <div class="content"><p >{{blog.content}}</p></div>
            <hr>
            <h4>作者：</h4>
            <p>{{blog.author}}</p>
            <hr>
            <h4>博客分类：</h4>
            <ul>
              <li v-for="type in blog.blogTypes">
                {{type}}
              </li>
            </ul>
            <hr>
            <h4>发布时间：</h4>
            <p>{{blog.time}}</p>
            <hr>
            <br>
            <Button type="error" @click="info" v-if="!delicon">删除</Button>
            <Button  @click="pass" v-if="delicon">取消</Button>
            <Button type="error" v-if="delicon" v-on:click="delBlog">确认删除</Button>
            <Button type="info"><router-link :to="'/edit/'+id">编辑</router-link></Button>
            </Card>
     <!-- <h1></h1>
     <p>{{blog.content}}</p>
     <p>{{blog.author}}</p>
     <p>分类：</p>
     <ul>
       <li v-for="type in blog.blogTypes">
         {{type}}
       </li>
     </ul>
    <button v-on:click="delBlog">删除</button>
    <router-link :to="'/edit/'+id"><button>编辑</button></router-link> -->
   </div>
</template>
 
<script>  
export default {
  name: 'singleBlog',
  data () {
    return {
      id:this.$route.params.id,
      blog:{},
      delicon:false
    }
  },
  created(){
    this.$http.get('https://wd0181130895wpybmf.wilddogio.com/posts/'+this.id+".json").then(function(data){  
      //console.log(data.json());
      return data.json();
      
      
      //this.blog=data.body;
    }).then(function(data){
      this.blog=data;
    })
  },
 methods:{
    delBlog:function(){
      this.$http.delete('https://wd0181130895wpybmf.wilddogio.com/posts/'+this.id+".json").then(function(respone){this.$router.push({path:'/'})});
      this.$Message.info('删除成功！')
    },
    info (){
        this.$Message.info('确定要删除？');
        this.delicon=true;

    },
    pass(){
        this.delicon=false;

    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#single_blog{
  max-width: 700px;
  margin:20px auto;
  padding: 20px;
  background-color: #eee;
  border: 1px dotted #aaa;
}
p{
  display: inline;
  line-height: 30px;
}
h3,h4,ul,li{
  display: inline;
  line-height: 40px;
}
.content{
  display: block;
  width: 100%;
  min-height: 200px;
}
a{
  color: #fff;
}
Button{
  margin-right: 10px;
}
</style>



