<template>
    <div id="add_blog" style="background:#eee;padding: 20px">
      <h2>添加博客</h2>
      <form v-if="!submitd">
        <label>博客标题</label>
        <Input v-model="blog.title" required placeholder="Enter title..." style="width: 100%" />
        <label>博客内容</label>
        <Input v-model="blog.content" type="textarea" :rows="5" placeholder="Enter content..." />
  <div id="checkbox">
    <CheckboxGroup v-model="blog.blogTypes"  size="large">
        <checkbox label="生活">
            <Icon type="ios-shirt" />
            <span>生活</span>
        </Checkbox>
        <Checkbox label="娱乐">
            <Icon type="md-musical-note" />
            <span>娱乐</span>
        </Checkbox>
        <Checkbox label="运动">
            <Icon type="ios-basketball" />
            <span>运动</span>
        </Checkbox>
        <Checkbox label="心得">
            <Icon type="md-heart" />
            <span>心得</span>
        </Checkbox>
    </CheckboxGroup>
  </div>
      <label>作者</label>
      <Select v-model="blog.author" style="width:100%">
        <Option v-for="author in authors" :value="author" :key="value">
            <span>{{author}}</span>
        </Option>
    </Select>
      <Button type="error" v-on:click.prevent="post" size="large" >添加博客</Button>
      </form>
      <div v-if="submitd">
        <Alert type="success" show-icon>
        <h3>博客发布成功!</h3>
        <span slot="desc"></span>
        </Alert>
      </div>

      
      
      <div class="preview">
        <Card :bordered="false">
            <h2>博客预览</h2>
            <h4>博客标题:</h4>
            <p>{{blog.title}}</p>
            <hr>
            <h4>博客内容:</h4>
            <p>{{blog.content}}</p>
            <hr>
            <h4>博客分类：</h4>
            <ul>
              <li v-for="type in blog.blogTypes">
                {{type}}
              </li>
            </ul>
            <hr>
            <h4>作者：</h4>
            <p>{{blog.author}}</p>
        </Card>
    </div>
    </div>
</template>
 
<script>  
export default {
  name: 'Addblog',
  data () {
    return {
      blog:{
        title:'',
        content:'',
        blogTypes:[],
        author:'',
        time:''
      },
      submitd:false,
      authors:['apeng','gouyang','shaobin']
    }
  },
  methods:{
//     mydate.getDate(); //获取当前日(1-31)
// mydate.getDay(); //获取当前星期X(0-6,0代表星期天)
// mydate.getTime(); //获取当前时间(从1970.1.1开始的毫秒数)
// mydate.getHours(); //获取当前小时数(0-23)
// mydate.getMinutes(); //获取当前分钟数(0-59)
// mydate.getSeconds();
    post:function(){
      if(this.blog.title==""||this.blog.title.indexOf(" ")!=-1){
        this.$Notice.error({title: '请输入标题！',});
        return;
      }
      var date=new Date();
      this.blog.time=date.getFullYear()+"."+(date.getMonth()+1)+"."+date.getDate()+" "+date.getHours()+":"+date.getMinutes();
      //console.log(this.blog.time);
      this.$http.post('https://wd0181130895wpybmf.wilddogio.com/posts.json',this.blog).then(function(data){
        // console.log(data);
        this.submitd=true;
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add_blog *{
  box-sizing: border-box;
  list-style: none;
}
h4,p{
  display: inline;
  line-height: 40px;
}
.preview ul, .preview li{
  display: inline;
  line-height: 40px;
}
#add_blog{
  box-sizing: border-box;
  margin: 20px auto;
  max-width: 700px;
  padding: 20px;
  padding-top: 0px;
}
label{
  display: block;
  padding: 10px 0 10px;
}
input[type="text"],select,textarea{
  display: block;
  padding: 10px;
  width: 100%;
}
textarea{
  height: 200px;
}
#checkbox input,#checkbox label{
  display: inline-block;
}
#checkbox input{
  margin-right: 20px;
}
Button{
  margin:20px 0;
}

</style>



